import React, { Component, Fragment } from 'react';
import isEmpty from 'lodash.isempty';

// examples:
import GoogleMap from '../components/GoogleMap';

// consts: [34.0522, -118.2437]
import United_States from '../const/la_center';

const getInfoWindowString = place => `
    <div>
      <div style="font-size: 16px;">
        ${place.username}
      </div>
      <div style="font-size: 14px;">
        <span style="color: grey;">
        ${place.accountName}
        </span>
        <span style="color: orange;">${String.fromCharCode(9733).repeat(
          Math.floor(place.rating)
        )}</span><span style="color: lightgrey;">${String.fromCharCode(
  9733
).repeat(5 - Math.floor(place.rating))}</span>
      </div>
      <div style="font-size: 14px; color: grey;">
        
      </div>
      <div style="font-size: 14px; color: grey;">
        ${'$'.repeat(place.price_level)}
      </div>
      <div style="font-size: 14px; color: green;">
        
      </div>
    </div>`;

// Refer to https://github.com/google-map-react/google-map-react#use-google-maps-api
const handleApiLoaded = (map, maps, places) => {
  const markers = [];
  const infowindows = [];

  places.forEach(place => {
    markers.push(
      new maps.Marker({
        position: {
          lat: place.lat,
          lng: place.lng
        },
        map
      })
    );

    infowindows.push(
      new maps.InfoWindow({
        content: getInfoWindowString(place)
      })
    );
  });

  markers.forEach((marker, i) => {
    marker.addListener('click', () => {
      infowindows[i].open(map, marker);
    });
  });
};

class MarkerInfoWindowGmapsObj extends Component {
  constructor(props) {
    super(props);

    this.state = {
      places: []
    };
  }

  componentDidMount() {
    fetch('https://www.ireportapp.com/v2/userlogs/getLocations')
      .then(response => response.json())
      .then(data => {
        data.results.forEach(result => {
          result.show = false; // eslint-disable-line no-param-reassign
        });
        this.setState({ places: data.results });
      });
  }

  render() {
    const { places } = this.state;
    console.log(places);
    return (
      <Fragment>
        {!isEmpty(places) && (
          <GoogleMap
            defaultZoom={4.9}
            defaultCenter={United_States}
            bootstrapURLKeys={{ key: process.env.REACT_APP_MAP_KEY }}
            yesIWantToUseGoogleMapApiInternals
            onGoogleApiLoaded={({ map, maps }) =>
              handleApiLoaded(map, maps, places)
            }
          />
        )}
      </Fragment>
    );
  }
}

export default MarkerInfoWindowGmapsObj;



